.bg {
	background: linear-gradient(rgba(255, 255, 255, 0.3), rgba(254, 239, 251)), url(./wallpaper.jpg);

	background-size: auto 100%;
	background-position: center;
	padding-top: 56.25%;
}

@media (orientation:landscape ){
	.bg {
		position: absolute;
		background: 90deg, linear-gradient(rgba(255, 255, 255, 0.3), rgba(254, 239, 251)), url(./wallpaper.jpg);

		position: fixed;
		left: 0;
		top: 0;
		width: 30%;
		height: 100%;
		background-position: center;
	}
}